import React, {useRef} from "react"
import Title from "../UI/UIComponents/Title";
import TextInput from "../UI/Form/TextInput";
import {useNavigate} from "react-router-dom";
import PageContent from "../UI/Layout/PageContent";
import {Form, Formik} from "formik";
import SubmitButton from "../UI/Form/SubmitButton";
import axios from "axios";
import Links from "../../Util/Links";
import {Toast} from "primereact/toast";

export default function (props) {
    const navigate = useNavigate()
    const toast = useRef()

    return (
        <Formik initialValues={{login: "", pwd: ""}}
                onSubmit={(values, {setSubmitting}) => {
                    console.log('submit')
                    axios.post(Links.login, values)
                        .then(rs => {
                            if (rs.data.found) {
                                window.localStorage.setItem("login_data", JSON.stringify(rs.data))
                                switch (rs.data.role) {
                                    case "MANAGER":
                                        navigate("/manager_home")
                                        break;
                                    case "PARTNER":
                                        navigate("/partner_profile")
                                        break;
                                    case "CLIENT":
                                        navigate("/client_profile")
                                        break;
                                }

                            } else {
                                alert(" /")
                                setSubmitting(false)
                            }
                        })
                }}
        >
            {({isSubmitting}) => (
                <Form>
                    <Toast ref={toast}/>
                    <PageContent alignItems={"stretch"}>
                        <Toast ref={toast}/>
                        <Title text={""}/>
                        <TextInput label={":"} name={"login"}/>
                        <TextInput label={":"} type="password" name={"pwd"}/>
                        <SubmitButton text={""}/>
                    </PageContent>
                </Form>
            )}
        </Formik>
    )
}